import Vue from 'vue'
import router from './router'
import store from './store'
const APP_NAME = require('../package.json').name
Vue.__share_pool__ = (Vue.__share_pool__ || {})
Vue.__share_pool__.childRoutes = (Vue.__share_pool__.childRoutes || {})
Vue.__share_pool__.childStores = (Vue.__share_pool__.childStores || {})
Vue.__share_pool__.childStores[APP_NAME] = store
Vue.__share_pool__.childRoutes[APP_NAME] = router


console.log("Vue.__share_pool__.childStores[APP_NAME]===>", Vue.__share_pool__.childStores[APP_NAME])
console.log("Vue.__share_pool__.childRoutes[APP_NAME]===>", Vue.__share_pool__.childRoutes[APP_NAME])


